import React from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CForm,
    CFormGroup,
    CFormText,
    CTextarea,
    CInput,
    CInputFile,
    CInputCheckbox,
    CInputRadio,
    CLabel,
    CSelect,
    CRow,
    CSwitch
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { DocsLink } from 'src/reusable'
import { useSelector } from 'react-redux'

function AddCatagory() {
    const [collapsed, setCollapsed] = React.useState(true)
    const [showElements, setShowElements] = React.useState(true)
    const [CatagoryName, setCatagoryName] = React.useState("")
    const [CatagoryPrice, setCatagoryPrice] = React.useState("")
    const [SelectedCatagory, setSelectedCatagory] = React.useState("")
    

    const userEmail = useSelector(state => state.user.email)
    return (
        <CRow>
            <CCol xs="12" md="12">
                <CCard>
                    <CCardHeader>
                        Add Catagory
                        <small> Inventory</small>
                    </CCardHeader>
                    <CCardBody>
                        <CForm action="" method="post" encType="multipart/form-data" className="form-horizontal">
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel>Email</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <p className="form-control-static">{userEmail && userEmail}</p>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="text-input">Catagory Name</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CInput id="text-input" name="text-input" placeholder="Text" />
                                    <CFormText>Catagory Display Name</CFormText>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="textarea-input">Catagory Description</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CTextarea
                                        name="textarea-input"
                                        id="textarea-input"
                                        rows="9"
                                        placeholder="Content..."
                                    />
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol tag="label" sm="3" className="col-form-label">
                                    In Stock
                                </CCol>
                                <CCol sm="9">
                                    <CSwitch
                                        className="mr-1"
                                        color="primary"
                                        defaultChecked
                                        shape="pill"
                                    />
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CLabel col md={3}>Catagory Image (Optional)</CLabel>
                                <CCol xs="12" md="9">
                                    <CInputFile custom id="custom-file-input" />
                                    <CLabel htmlFor="custom-file-input" variant="custom-file">
                                        Choose file...
                                    </CLabel>
                                </CCol>
                            </CFormGroup>
                        </CForm>
                    </CCardBody>
                    <CCardFooter>
                        <CButton type="submit" size="sm" color="primary"><CIcon name="cil-scrubber" /> Submit</CButton>
                        <CButton type="reset" size="sm" color="danger"><CIcon name="cil-ban" /> Reset</CButton>
                    </CCardFooter>
                </CCard>
            </CCol>
        </CRow>
    )
}

export default AddCatagory
